














































 
var view
export default {
	data() {
		return {
			status:'10',
			taskVoList:[],
			curindex:1,
			searchname:''
		};
	},
	created:function(){
		view = this;
	},
	mounted:function(){
		this.getList()
	},			
	watch:{
		searchname:function(){
			if(this.searchname){
				this.getList()
			}
		}
	},
	methods:{
		shuaixuan:function(){
			uni.navigateTo({
				url: '../shuaixuan/shuaixuan'
			})
		},
		stateqiehuan:function(index){
			this.curindex = index
			if(index ==1){
			   this.status = '10'
			}else{
			   this.status = '20'
			}
			this.getList()
		},
		getList:function(){
			var msg ={
				page:'1',
				pageSize:'10',
				StreetOfficeId:'',
				status:this.status,
				startDateBefore: '',
				startDateAfter: '',
				endDateBefore: '',
				endDateAfter: '',
				createTimeBefore: '',
				createTimeAfter: ''
			}
			this.$request({
				url: 'findSuperviseTaskList',
				method:'post',
				showLoading:true,
				loadingText:'加载中',
				data:msg,
				success:(jsonData) => {
					console.log(6666)
					console.log(jsonData)
					if(jsonData.status){
						 this.taskVoList = jsonData.data
					}else{
						uni.showToast({
							icon:'none',
							title:jsonData.msg,
							duration: 1000
						});
					}
				},
				complete() {
					view.loading = false;
				}
			});
		}
	}
}
